import Axios from 'axios';
import React, { Component } from 'react';
import { Col, Row } from 'react-bootstrap';
import { Link, withRouter } from "react-router-dom";
import AppUrl from "../../appurl/AppUrl";
import NoImage from '../../asset/image/noimage.png';
import CategoryLayout from '../../hoc/categoryLayout';
import Loading from "../Loading/Loading";


class cartegory extends Component {
    constructor() {
        super();
        this.state = {
            catname:'product-market',
            data:[],
            loading:true
        }
    }

    componentDidMount() {
        const myFormData = new FormData()
        myFormData.append('p_slug',this.state.catname)

        const config = {
            headers : {'content-type':'multipart/form-data'}
        }
        Axios.post(AppUrl.productMarketCategory,myFormData,config)
            .then(res=>{
                this.setState({data:res.data,loading:false})
                console.log(res.data+"Data List")
            })
            .catch(error=>{
                console.log(error)
            })
    }

    render() {

         const CategoryList = this.state.data.map((category) => (
            <Col>
                <Link to={"/category/products/"+category.cat_slug}>
                    <div className="category_list">

                    {category.image ?<img className="category_item_pic" loading="lazy" src={AppUrl.photoUrl+category.image} alt={category.cat_name}/>
                    : <img className="category_item_pic" src={NoImage} alt={category.cat_name}/>}
                    <p className="text-center mt-2 category_name">{category.cat_name}</p>
                    </div>
                </Link>
            </Col>   
        ));


        return (
            <CategoryLayout className="bg-white">
              <Col><h2 className="text-center">FIND YOUR ITEMS BY CATEGORIES</h2></Col>
                            <Row lg={5} md={4} sm={2} xs={2} className="my-5">
                            {this.state.loading==true ? <Loading/>:CategoryList}
                            </Row>
            </CategoryLayout>
        );
    }
}

export default withRouter(cartegory);