import { faClock, faEnvelopeOpen, faMapMarkedAlt, faPhoneAlt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Axios from 'axios';
import React, { Component, Fragment } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { toast, ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import AppUrl from "../../../appurl/AppUrl";
import classes from './footer.module.css';

class footer extends Component {
    constructor() {
        super();
        this.state={
            name:'',
            email:'',
            subject:'',
            msg:''
        }
    }
    componentDidMount() {
    }

    onName(e){
        this.setState({name:e.target.value})
    }
    onEmail(e){
        this.setState({email:e.target.value})
    }
    onSubject(e){
        this.setState({subject:e.target.value})
    }
    onMsg(e){
        this.setState({msg:e.target.value})
    }
    FormSubmit(event){
        const formData = new FormData()
        formData.append('name',this.state.name)
        formData.append('email',this.state.email)
        formData.append('subject',this.state.subject)
        formData.append('msg',this.state.msg)
        Axios.post(AppUrl.ClentMsgSend, formData)
            .then(res=>{
                if(res.data.success){
                    this.setState({name:'',email:'',subject:'',msg:''})
                    toast.success(res.data.success, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }else{
                    toast.error(res.data.error, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }
            })
            .catch(error=>{
                console.log(error)
            })
        event.preventDefault()
    }

    render() {
        return (
            <Fragment>
                <footer className={['py-5', classes.footer_area].join(' ')}>
                    <div className="footer-big">
                        <ToastContainer
                            position="top-right"
                            autoClose={5000}
                            hideProgressBar={false}
                            newestOnTop={false}
                            closeOnClick
                            rtl={false}
                            pauseOnFocusLoss
                            draggable
                            pauseOnHover
                        />
                        {/* Same as */}
                        <ToastContainer />
                            <div className="footer_container">
                            <Container>
                             <div className="footer_top">
                                <Row lg={4} md={2} sm={1} xs={1}>
                                    <Col>
                                    <div class="banner-single">
                                        <div class="panel">
                                        <i class="fa fa-truck" aria-hidden="true"></i>
                                        <div class="block">
                                            <h4 class="title">Free Shipping</h4>
                                            <p>On order over 3000 TK</p>
                                        </div>
                                        </div>
                                    </div>
                                    </Col>

                                    <Col>
                                    <div class="banner-single">
                                        <div class="panel">
                                        <i class="fa fa-money" aria-hidden="true"></i>
                                        <div class="block">
                                            <h4 class="title">Money Back Guarantee</h4>
                                            <p>Conditions Apply</p>
                                        </div>
                                        </div>
                                    </div>
                                    </Col>

                                    <Col>
                                    <div class="banner-single">
                                        <div class="panel">
                                        <i class="fa fa-life-ring" aria-hidden="true"></i>
                                        <div class="block">
                                            <h4 class="title">Support 24/7</h4>
                                            <p>01310850684, 01310850685</p>
                                        </div>
                                        </div>
                                    </div>
                                    </Col>

                                    <Col>
                                    <div class="banner-single last">
                                        <div class="panel">
                                        <i class="fa fa-credit-card" aria-hidden="true"></i>
                                        <div class="block">
                                            <h4 class="title">Safe Payment</h4>
                                            <p>Protect online payment</p>
                                        </div>
                                        </div>
                                    </div>
                                    </Col>
                                </Row>
                            </div>   



                            <Row lg={4} md={2} sm={1} xs={1} className={classes.footer_row}>
                                <Col>
                                    <h5 className="text-light">Contact US</h5>
                                    <div className="bottom-line">
                                    </div>
                                    <li>
                                        <FontAwesomeIcon icon={faMapMarkedAlt} className="mr-2"/>
                                        <span className="text-light">Shyamoli Square Shopping Mall, 4th Floor, Shop No.466, Dhaka -1207</span>
                                    </li>
                                    <li>
                                        <FontAwesomeIcon icon={faPhoneAlt} className="mr-2"/>
                                        <span className="text-light">01310850684, 01310850685</span>
                                    </li>
                                    <li>
                                        <FontAwesomeIcon icon={faEnvelopeOpen} className="mr-2"/>
                                        <span className="text-light">customer@trust-lite.com</span>
                                    </li>
                                    <li>
                                        <FontAwesomeIcon icon={faClock} className="mr-2"/>
                                        <span className="text-light"><strong>Saturday - Friday:</strong> 09.00 AM - 11:00 PM</span>
                                    </li>
                                    <div className="social_profiles">

                                            <a href="https://www.facebook.com/trustlite.com.bd/" target="_blank">
                                                <i className="fab fa-facebook"></i>
                                            </a>

                                            <a href="https://twitter.com/TrustLite" target="_blank">
                                                <i className="fab fa-twitter"></i>
                                            </a>

                                            <a href="https://www.linkedin.com/in/trust-lite-b7aa45205/" target="_blank">
                                                <i className="fab fa-linkedin"></i>
                                            </a>

                                            <a href="https://www.instagram.com/trustlite103/" target="_blank">
                                                <i className="fab fa-instagram"></i>
                                            </a>

                                            <a href="https://www.facebook.com/groups/trustliteshopping" target="_blank">
                                            <i className="fab fa-youtube-square"></i>
                                            </a>
                                
                            
                                        </div>
                                </Col>

                                <Col>
                                    <h5 className="text-light">General information</h5>
                                    <div className="bottom-line">
                                    </div>
                                    <ul>
                                            <Link to="/overview"><li>Compnay Overview</li></Link>
                                            <Link to="/our/team"><li>Our Teams</li></Link>
                                            <Link to="/refund-policy"><li>Return and refund Policy</li></Link>
                                            <Link to="/terms-condition"><li>Terms &amp; Conditions</li></Link>
                                            <Link to='/privacy-policy'><li>Privacy Policy</li></Link>
                                    </ul>
                                </Col>

                                <Col>
                                    <h5 className="text-light">Our Mobile App</h5>
                                    <div className="bottom-line">
                                    </div>
                                    <div className="app_section">
                                        <Row>
                                            <Col md={4}>
                                                <img src="https://agamishop.com/ecommerce/apple.png" alt="apple-store"/>
                                            </Col>
                                            <Col md={8}>
                                                <span>Download From The</span>
                                                <p>app store</p>
                                            </Col>
                                        </Row>
                                        
                                         <Row>
                                            <Col md={4}>
                                                <img src="https://agamishop.com/ecommerce/playstore.png" alt="google-store"/>
                                            </Col>
                                            <Col md={8}>
                                                <span>Download From The</span>
                                                <p>app store</p>
                                            </Col>
                                        </Row>

                                    </div>
                                </Col>

                                <Col>
                                    <h5 className="text-light">Do you have any question?</h5>
                                    <div className="bottom-line">
                                    </div>
                                    <form onSubmit={this.FormSubmit.bind(this)} >
                                            <div className="form-group">
                                                <input
                                                    type="text" value={this.state.name} required onChange={this.onName.bind(this)}
                                                    className="form-control"
                                                    placeholder="Your Name"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="text" required onChange={this.onEmail.bind(this)}
                                                    className="form-control" value={this.state.email}
                                                    placeholder="Phone or Email"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="text" required onChange={this.onSubject.bind(this)}
                                                    className="form-control" value={this.state.subject}
                                                    placeholder="Subject"
                                                />
                                            </div>
                                            <div className="form-group">
                                    <textarea
                                        row="3" required onChange={this.onMsg.bind(this)}
                                        className="form-control" value={this.state.msg}
                                        placeholder="Your Message"
                                    />
                                            </div>
                                            <button
                                                className={[classes.btn_block, 'btn btn-primary col-6 float-right'].join(' ')}
                                                type="submit"
                                            >
                                                Submit
                                            </button>
                                        </form>
                                </Col>
                                
                            </Row>
                            </Container>
                            </div>
                    </div>
                </footer>
            </Fragment>
        );
    }
}

export default footer;
